;(function(){
  angular.module('triflix')
  .component('multiplayer', {
    bindings: {
    },
    templateUrl: 'app/components/panels/multiplayer/multiplayer.html',
    controller: multiplayerCtrl,
    controllerAs: 'multiplayer'
  });

  multiplayerCtrl.$inject = ['$scope', 'ApiPath', 'UserService', '$uibModal', 'PanelService'
  , 'SocketService', '$rootScope', 'PanelService', 'GameSettings'];

  function multiplayerCtrl($scope, ApiPath, UserService, $uibModal, PanelService,
    SocketService, $rootScope, PanelService, GameSettings){

    var self = this;
    self.user = UserService.getUser();
    var user = self.user;
    if(!user) return $scope.$parent.$close(); //display you have to login to use this feature
    if(!user.facebook) return $scope.$parent.$close();
    //gestire timeout per connessione assente

    SocketService.initSocket();

    var openBoard = function(){
      PanelService.open({
        component: 'board',
        scope: $scope
      });
    }

    self.fbFriends = [];
    /*funzione di paging, che scarica man mano tutti gli amici che hanno il
    gioco installato*/
    var getfbFriends  = function(params){
      return UserService.getFBfriendsWithApp(params)
      .then(function(resp){
        [].push.apply(self.fbFriends, resp.data.data);
        if(resp.data.paging.next)
          getfbFriends({nextPage: resp.data.paging.next});
      }, function(err){
        console.log(err);
      })
    }


    self.chooseOpponent = function(opponent){
      if(self.users){
        var opponentSocketId = SocketService.getOpponentSocketFromValue(opponent, self.users);
        SocketService.emit('choose opponent', {team: GameSettings.getSettings().team, opponentSocketId: opponentSocketId});
      }
    }

    getfbFriends()
    .then(function(){
      SocketService.emit('add to room', {
        userId: user.facebook.id,
        data: user
      });

      SocketService.on('add to room', function(data){
        $scope.$apply(function(){
          self.users = data.users;
          self.usersOnline = _.map(data.users, function(value, key){
            value.online = true;
            return value;
          })
          //mergio gli array e sovrascrivo eventuali proprietà online = false
          self.usersOnline = mergeByProperty(self.fbFriends, self.usersOnline, 'id');
          //self.usersOnline = [self.usersOnline[0], self.usersOnline[0], self.usersOnline[0], self.usersOnline[0]];
        })
      });
    })


    SocketService.on('challenge request', function(data){
      var text = 'Challenge request from '+ data.opponent.data.facebook.name;
      $uibModal.open({
        animation: true,
        component: 'confirm',
        backdrop: 'static',
        resolve: {
          text: [function(){
            return text;
          }],
          okCb: [function(){
            return function(){
              SocketService.emit('challenge accepted', {
                accepter: SocketService.getSocketId(),
                challenger: data.opponentSocketId
              })
              SocketService.chooseOpponent(data);
              $rootScope.$emit('triflix.game.start');
              openBoard();
            }
          }]
        }
      });
    });

    SocketService.on('challenge accepted', function(opponent){
      SocketService.chooseOpponent(opponent);
      $rootScope.$emit('triflix.game.start');
      openBoard();
    })

    this.openProfile = function(userSelected){
      PanelService.open({
        component: 'profile',
        scope: $scope,
        resolve: {
          user: function(){
            return userSelected;
          }
        }
      });
    }

    this.inviteFriend = function(friend){
      UserService.invite({
        inviteFrom: self.user.facebook.id,
        inviteTo: friend.data.facebook.id
      })
    }

    function mergeByProperty(arr1, arr2, prop){
      _.each(arr2, function(arr2Obj){
        var found = _.find(arr1, function(arr1Obj){
          if(arr1Obj.data.facebook[prop] === arr2Obj.data.facebook[prop])
            return arr1Obj;
        });
        found ? _.extend(found, arr2Obj) : arr1.push(arr2Obj);
      })
      return arr1;
    }
    this.params = {
      back: {
        text: 'Back',
        cb: function(){
          SocketService.removeOpponent();
          $scope.$parent.$close();
        }
      }
    };


  }
})();

;(function(){
  angular.module('triflix')
  .component('profile', {
    bindings: {
      user: '<'
    },
    templateUrl: 'app/components/panels/profile/profile.html',
    controller: profileCtrl,
    controllerAs: 'profile'
  });

  profileCtrl.$inject = ['$scope', 'ScoreService', 'UserService', 'PanelService'];

  function profileCtrl($scope, ScoreService, UserService, PanelService){
    var self = this;
    self.user = self.user.data || self.user;
    ScoreService.get(self.user.facebook.id)
    .then(function(resp){
      self.scores = resp.data;
      extractOpponents(self.scores);
      //return UserService.extUser(self.scores[0].player1);
      //risolvi con $q tutti gli utenti con cui ha fatto partite
      //oppure recupera questa informazione direttamente con una select su node
    })
    .then(function(resp){
      var p = resp;
    })
    .catch(function(err){
      console.log(err);
    })

    this.params = {
      back: {
        text: 'Back',
        cb: function(){
          $scope.$parent.$close();
        }
      }
    };

    this.openGameStats = function(score){
      PanelService.open({
        component: 'game-stats',
        scope: $scope,
        resolve: {
          score: function(){
            return score;
          }
        }
      });
    }

    function extractOpponents(scores){
      for(var i = 0; i < scores.length; i++){
        if(scores[i].player1.facebook && scores[i].player1.facebook.id !== self.user.facebook.id)
          scores[i].opponent = scores[i].player1;
        if(scores[i].player2.facebook && scores[i].player2.facebook.id !== self.user.facebook.id)
          scores[i].opponent = scores[i].player2;
      }
    }
  }
})();

;(function(){
  angular.module('triflix')
  .component('login', {
    bindings: {
    },
    templateUrl: 'app/components/panels/login/login.html',
    controller: loginCtrl,
    controllerAs: 'login'
  });

  loginCtrl.$inject = ['$scope', 'UserService', '$timeout', 'PanelService', 'TEAMS',
  'ANIMATIONS', 'ApiPath'];

  function loginCtrl($scope, UserService, $timeout, PanelService, TEAMS, ANIMATIONS,
  ApiPath){
    checkIncognitoMode();
    var isIncognitoMode = false;
    this.teams = TEAMS;
    this.animations = ANIMATIONS;
    var openPanel= function(panel){
      PanelService.open({
        component: panel,
        scope: $scope
      });
    }

    var getUser = function(panel){
      UserService.user()
      .then(function(resp){
        this.user = resp.data;
        if(panel)
          $timeout(function(){
            openPanel(panel);
          }, 100);
      }, function(err){
        console.log(err);
      })
    }
    getUser('');

    this.continueNotLogged = function(){
      openPanel('board');
    }

    this.loginWithFacebook = function(){
      if(window.localStorage.getItem('x-auth'))
        getUser('multiplayer');
      else
        facebookAuth();
    }

    this.openSettings = function(){
      openPanel('settings');
    }

    var facebookAuth = function(){
      goTo(ApiPath.login.remote);
    }

    /*workaround per fare settare al browser l'header referer fondamentale al login
    a node per fare il redirect corretto. con window.location non viene settato l'header referer*/
    function goTo(url){
      var a = document.createElement("a");
      if(!a.click) //for IE
      {
           window.location = url;
           return;
      }
      a.setAttribute("href", url);
      a.style.display = "none";
      document.body.appendChild(a);
      a.click();
    }

    /*utile perchè in modalità incognito non funziona il localstorage*/
    function checkIncognitoMode(){
      var fs = window.RequestFileSystem || window.webkitRequestFileSystem;
      if (!fs) {
        console.log('check failed');
        isIncognitoMode = false;
      } else{
        fs(window.TEMPORARY,
           100,
           function(){
             isIncognitoMode = false; //not in incognito
           },
           function(){
             isIncognitoMode = true; //incognito mode
             getUser('multiplayer');
           });
      }
    }
  }
})();

