;(function(){
  angular.module('triflix')
  .component('chat', {
    bindings: {

    },
    templateUrl: 'app/components/chat/chat.html',
    controller: chatCtrl
  });

  chatCtrl.$inject = ['$element', '$scope', '$timeout', '$document', 'UserService',
  'SocketService'];


  function chatCtrl($element, $scope, $timeout, $document, UserService, SocketService){
    var self = this;
    self.messages = [];


    SocketService.on('msg to user', function(data){
      $scope.$apply(function(){
        self.messages.push(data);
        if(self.chatOpened)
          self.lastMsgRead = true;
        else
          self.lastMsgRead = false;
      })
    })

    self.addMessage = function(e){
      var chatContainer = $element.find('.chat__container').first();
      self.user = UserService.getUser();
      var newMessage = {
        img: self.user.facebook.img,
        name: self.user.facebook.name,
        msg: self.message
      }
      self.messages.push(newMessage);
      e.preventDefault(); //impedisco all'invio di andare a capo
      //aspetto la fine di questo digest così si aggiorna il div
      //con la nuova altezza e posso scrollare fino in basso
      $timeout(function(){
        chatContainer[0].scrollTop = chatContainer[0].scrollHeight; //scrollo giù
      },0, false);
      SocketService.emit('msg to user', _.extend({
        socketTo: SocketService.getOpponent().opponentSocketId,
      }, newMessage))
      self.message = "";
    }

    self.$postLink = function(){
      var chatLogo = $element.find('.chat__logo').first();
      var chat = $element.find('.chat').first();
      var textArea = $element.find('.chat textarea').first();

      chat.on('click', function (e){
        $scope.$apply(function(){
          e.stopPropagation();
          var chatOpened = true;
          self.lastMsgRead = true;
          chat.removeClass('chat__closed').addClass('chat__opened')
          $timeout(function(){self.chatOpened = chatOpened}, 1000);
        })
      });


      $document.on('click', handlerDocClick);

      $scope.$on('$destroy', function(){
        $document.off('click', handlerDocClick);
      })

      function handlerDocClick(){
        $scope.$apply(function(){
          if(self.chatOpened){
            self.chatOpened = false;
            chat.removeClass('chat__opened').addClass('chat__closed');
          }
        })
      }

    }
  }

})();

;(function(){
  angular.module('triflix')
  .component('panelContainer', {
    bindings: {
    },
    controller: panelContainerCtrl,
    controllerAs: 'panel'
  });

  panelContainerCtrl.$inject = ['$element', '$scope', '$rootScope', '$templateCache', '$compile',
   '$window', '$timeout'];

  function panelContainerCtrl($element, $scope, $rootScope, $templateCache, $compile,
    $window, $timeout){


      var width, height, leftPos;

      function initWindow(){
        width = window.innerWidth
        || document.documentElement.clientWidth
        || document.body.clientWidth;

        height = window.innerHeight
        || document.documentElement.clientHeight
        || document.body.clientHeight;
      }
      initWindow();

      this.$onInit = function(){
        $element.css('position', 'absolute');
        $element.css('width', width);
        $element.css('height', height);
        $element.css('top', '0px');
        $element.css('left', -width + 'px');
      }

      var panels = [];
      $rootScope.$on('triflix.addpanel', function(evt, params){
        var newScope = $rootScope.$new();
        var elem = angular.element('<div></div>');
        elem.css('float', 'left');
        elem.css('width', width);
        elem.css('height', height);

        if(params.controller){
          var ctrl = angular.element('<div></div>');
          ctrl.attr('ng-controller', params.controller);
          var template = $templateCache.get(params.templateUrl);
          ctrl.append(angular.element(template));
          elem.append(ctrl);
        }
        else if(params.component){
          var component = '<' + params.component;
          if(params.resolve && typeof(params.resolve) === "object"){
            for(var key in params.resolve){
              newScope['resolved' + key] = params.resolve[key]();
              component += ' ' + key + '="resolved' + key + '"';
            }
          }
          component += '></' + params.component + '>';
          elem.append(component);
        }


        newScope.prototype = params.scope;
        newScope.$close = function(){
          var index = panels.indexOf(panel);
          panels.splice(index, 1);
          panel.scope().$destroy();
          var leftPos = panels.length ? -width * (panels.length - 1) : 0;

          $element.css('left', leftPos);
          $timeout(function(){
            panel.remove();
            $element.css('width', width * panels.length);
          }, 500);

        }
        var panel = $compile(elem)(newScope);
        panels.push(panel);

        $element.parent().css('position', 'relative');
        $element.parent().css('overflow-x', 'hidden');
        $element.parent().css('width', width);
        $element.parent().css('height', height);

        $element.css('width', width * panels.length);
        $element.append(panel);
        var leftPos = -width * (panels.length - 1);

        $element.css('left', leftPos);

      });

      this.$postLink = function(){
        angular.element($window).on('resize', recalculateSize);
      }

      function recalculateSize(){
        initWindow();
        panels.forEach(function(panel){
          panel.css('height', height);
          panel.css('width', width);
        })

        $element.css('height', height);
        $element.css('width', width * panels.length);
        $element.css('left', (- width * (panels.length - 1)));
        $element.parent().css('height', height);
        $element.parent().css('width', width);
      }
  }
})();

