;(function(){
  angular.module('triflix')
    .factory('httpInterceptor', httpInterceptor);

    httpInterceptor.$inject = ['$location'];
    var authHeader = 'x-auth';
    function httpInterceptor($location){
      return {
        request: function(config){
          var auth = window.localStorage.getItem(authHeader);
          if(auth)
            config.headers[authHeader] = auth;
          return config;
        },
        response: function(response){
          var auth = response.headers()[authHeader];
          if(auth)
            window.localStorage.setItem(authHeader, auth);
          return response;
        },
        responseError: function(response){
          return response;
        }
      }
    }

})();

;(function(){
  angular.module('triflix')
  .service('GameSettings', GameSettings);

  GameSettings.$inject = ['TEAMS', 'LEVELS', '$rootScope'];
  /*setting panel for the game*/

  function GameSettings(TEAMS, LEVELS, $rootScope){

    var settings = fetchSettings();

    this.setTeam = function(team){
      settings.team = TEAMS[team.toUpperCase()] || TEAMS.X;
      $rootScope.$emit('triflix.game.change.settings');
      persistSettings();
    }
    this.setLevel = function(level){
      settings.level = LEVELS[level.toUpperCase()] || LEVELS.IMPOSSIBLE;
      $rootScope.$emit('triflix.game.change.settings');
      persistSettings();
    }
    this.getSettings = function(){
      return settings;
    }

    function persistSettings(){
      localStorage.setItem('settings', angular.toJson(settings));
    }
    function fetchSettings(){
      var fetchedSettings = localStorage.getItem('settings');
      if(fetchedSettings){
        return angular.fromJson(fetchedSettings);
      } else{
        return {
          team: TEAMS.X,
          level: LEVELS.IMPOSSIBLE,
          sound: 'off'
        }
      }
    }

  }
})();

;(function(){
  angular.module('triflix')
  .service('Game', Game);

  Game.$inject = ['$http', 'TEAMS', 'LEVELS', 'GameSettings', '$timeout', 'ApiPath', '$q',
   'SocketService', '$rootScope', 'TicTacToeWrapper'];

  function Game($http, TEAMS, LEVELS, GameSettings, $timeout, ApiPath, $q, SocketService,
    $rootScope, TicTacToeWrapper){

    var ticTacToeWrapper, self = this;
    game = {};
    game.team = GameSettings.getSettings().team;
    game.level = GameSettings.getSettings().level;
    game.state = ['','','','','','','','',''];
    game.winner = {};
    this.lockBoard = true;


    var AImove = function(game){
      var deferred = $q.defer();
      $timeout(function(){
        try{
          if(!self.hasEmptySlots(game.state)){
            return deferred.reject(new Error('draw'));
          }
          var result = ticTacToeWrapper.makeMove(game);
          deferred.resolve(result);
        }
        catch(error){
          deferred.reject(error);
        }
      }, 800);
      return deferred.promise;
    }

    var multiplayerMove = function(game, lastMove){
      var hasemptyslots = self.hasEmptySlots(game.state);
      self.winner(game);
      return SocketService.makeMove(game, lastMove, hasemptyslots);
    }
    this.opponentMove = function(game, lastMove){
      if(SocketService.getOpponent()){
        return multiplayerMove(game, lastMove);
      } else{
        return AImove(game);
      }

    }

    this.reset = function(){
      game.state = ['','','','','','','','',''];
      game.winner = {};
      ticTacToeWrapper = new TicTacToeWrapper();
    }

    this.flatCoordinate = function(x, y){
      return x + (y * 3);
    }

    this.getStatus = function(){
      return game;
    }

    this.hasEmptySlots = function(state){
      var foundOne = _.find(state, function(cell){return cell === TEAMS.EMPTY});
      return foundOne !== undefined ? true : false;
    }
    this.winner = function(game){
      game.winner = ticTacToeWrapper.winner(game);
      if(game.winner){
        game.winner.team = game.winner.cell;
        delete game.winner.cell;
      }
      return game.winner;
    }

    /* events */
    $rootScope.$on('triflix.game.change.settings', function(evt, data){
      game.team = GameSettings.getSettings().team;
      game.level = GameSettings.getSettings().level;
    })
  }
})();

;(function(){
  angular.module('triflix')
  .service('SocketService', SocketService);

  SocketService.$inject = ['ApiPath', '$timeout', '$q', 'TEAMS', '$rootScope'];

  function SocketService(ApiPath, $timeout, $q, TEAMS, $rootScope){

    var socket, opponent;
    this.initSocket = function(){
      if(!window.io)
        return console.log('socket.io not loaded');
      if(!socket){
        socket = io.connect(ApiPath.multiplayer.remote);
        this.on('disconnect', function(){
          socket = null;
        })
      }
    }
    this.emit = function(evt, data){
      socket.emit(evt, data);
    }
    this.on = function(evt, clb){
      socket.on(evt, clb);
    }
    this.getSocketId = function(){
        return socket.id;
    }
    this.chooseOpponent = function(challenger){
      opponent = challenger;
    }
    this.removeOpponent = function(){
      opponent = null;
    }
    this.getOpponentSocketFromValue = function(opponent, users){
      var opponentSocketId = -1;
      _.mapObject(users, function(user, socketId){
        if(user.userId === opponent.userId)
          opponentSocketId = socketId;
      });
      return opponentSocketId;
    }

    this.getOpponent = function(){
      return opponent;
    }

    this.makeMove = function(game, myLastMove, hasEmptySlots){
      var deferred = $q.defer();
      if(!hasEmptySlots){
        deferred.reject(new Error('draw'));
      }
      socket.emit('make move', {move: myLastMove, opponent: opponent});
      deferred.resolve({data: game});
      /*socket.on('make move', function(opponentMove){
        $rootScope.$apply(function(){
          game.state[opponentMove] = game.team === TEAMS.X ? TEAMS.O : TEAMS.X;
          deferred.resolve({data: game});
        })
      })*/
      return deferred.promise;
    }


  }
})();

